@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.animate-fade-in {
  animation: fade-in 0.5s ease-out forwards;
  opacity: 0;
}
.custom-scrollbar::-webkit-scrollbar {
  width: 6px;
}
.custom-scrollbar::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.05);
}
.custom-scrollbar::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #003A70, #00A8E8);
}
.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #00A8E8, #003A70);
}
@keyframes scroll-left {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}
.animate-scroll-left {
  animation: scroll-left 30s linear infinite;
}
.animate-scroll-left:hover {
  animation-play-state: paused;
}
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}
.animate-shimmer {
  animation: shimmer 3s infinite;
}
@keyframes grid-move {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(50px, 50px);
  }
}
@keyframes streak-1 {
  0% {
    transform: translateX(-100%) translateY(0) rotate(-45deg);
  }
  100% {
    transform: translateX(200%) translateY(100vh) rotate(-45deg);
  }
}
@keyframes streak-2 {
  0% {
    transform: translateX(-100%) translateY(30vh) rotate(-45deg);
  }
  100% {
    transform: translateX(200%) translateY(130vh) rotate(-45deg);
  }
}
@keyframes streak-3 {
  0% {
    transform: translateX(-100%) translateY(60vh) rotate(-45deg);
  }
  100% {
    transform: translateX(200%) translateY(160vh) rotate(-45deg);
  }
}
.animate-streak-1 {
  animation: streak-1 8s linear infinite;
}
.animate-streak-2 {
  animation: streak-2 10s linear infinite;
  animation-delay: 2s;
}
.animate-streak-3 {
  animation: streak-3 12s linear infinite;
  animation-delay: 4s;
